<template>
    <div>
        <el-row>
            <el-col>GET</el-col>
            <el-col :span="7">
                <el-form>
                    <el-form-item label="HOST" label-width="80px">
                        <el-input v-model="host"></el-input>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col>
                <el-switch v-model="download" inactive-text="结果保存为文件:"></el-switch>
            </el-col>
            <el-col>
                <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 15}" v-model="getUrls"
                          placeholder="输入网址，例如，['http://xxx','','']">
                </el-input>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-button type="primary" @click="analysis">解析</el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-form>
                    <el-form-item label="过滤条件(res.code，如果返回的结果中包含输入框中的值，就过滤)">
                        <el-input type="text" v-model="filter" size="small"></el-input>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <p>请求结果</p>
                <el-input type="textarea" :autosize="{ minRows: 10, maxRows: 15}" v-model="result">
                </el-input>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import http from '@/libs/http'
    import FileSaver from 'file-saver'

    export default {
        name: "api-get",
        data() {
            return {
                getUrls: "",
                download: false,
                result:"",
                filter: "",
                host: ""
            }
        },
        created(){
            this.host = localStorage.getItem("host") || "";
            this.filter = localStorage.getItem("filter") || "";
        },
        watch: {
            host(val){
                localStorage.setItem("host",val);
            },
            filter(val) {
                localStorage.setItem("filter",val);
            }
        },
        methods: {
            analysis() {
                let getArray = [];

                // console.log("get: ");
                // console.log(getArray);
                // let blob = new Blob([format.stringfy(getArray)], {type: "text/plain;charset=utf-8"});
                // FileSaver.saveAs(blob, "getUrls.txt");

                let getResultArray = [];
                let that = this;

                getArray = JSON.parse(this.getUrls);
                (function getFun(index) {
                    if (index < getArray.length) {
                        http.get(that.host + getArray[index]).then(res => {
                            if (that.filter.indexOf(res.code) === -1) {
                                getResultArray.push({url: getArray[index], res: res});
                            }
                            getFun(++index);
                        });
                    }
                    else {
                        if (getResultArray.length > 0) {
                            console.log(getResultArray);
                            if(that.download){
                                let blob = new Blob([JSON.stringify(getResultArray)], {type: "text/plain;charset=utf-8"});
                                FileSaver.saveAs(blob, "getResult.txt");
                            }
                            else {
                                that.result = JSON.stringify(getResultArray,null, 2);
                            }
                        }
                    }
                })(0);

            }
        }
    }

</script>

<style lang="scss">
    .bg-purple-dark {
        background: #99a9bf;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .bg-purple-light {
        background: #e5e9f2;
    }
</style>